@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
body {
  font-family: 'Poppins', sans-serif;
  font-weight: 300
}

h1,h2,h3 {
  font-weight: 300;
  margin: 30px 0;
}

img {
  width: 100%;
  margin-top: 35px;
  height: 95%;
}

.product-info{
  padding-left: 4em;
}

.vinyl-detail-back{
  display: inline-block;
  margin-right: 3em;
}

.btn{
  color: black
}

a, a:hover {
  color: inherit;
  text-decoration: none
}
.vinyl-card {
    margin-bottom: 40px;
}

.vinyl-card img {
    margin-bottom: 20px;
    height: 300px;
    object-fit: cover;
}

.vinyl-card text {
    display: flex;
    flex-direction: row;
    align-content: space-around;
}

.btn-add{
    margin-bottom: 20px;
}


